/*****************************************/
#breadcrumb {
	&.style1 {
		 ul li {
		 	font: 500 px_to_rem(15px) $fontmain; line-height: $lh;
		 	a {
		 		color: #333;
		 		&:hover {
		 			color: $colormain;
		 		}
		 	}
		 	&:first-of-type, &.c2 {
		 		a {
		 			color: $colormain;
		 		}
		 	}
		 }
		
	}
	.icon-ns {
		fill: currentColor;
	}
}



/*************************************/

.detailCT {
	@include clearfix;
	.slide_in_content {
		margin-bottom: 20px;
		float: left;
		width: 100%;
		img {
			width: 100%;
		}
	}
	h1 {
		font: 700 px_to_rem(32px) $fontmain; line-height: $lh;
		color: #333;
		margin-bottom: 10px;
		span {
		    font-size: 13px;
		    color: #fff;
		    background: #11affe;
		    padding: 2px 10px;
		    border-radius: 4px;
		    margin-right: 20px;
		    display: inline-block;
		    float: left;
		    margin-top: 12px;
		}
	}
	p.lead {
		font-family: Arial;
		font-size: px_to_rem(17px);
		line-height: $lhbody;
		font-weight: 400;
		color: #333;
		margin-bottom: 10px;
		float: left;
		.location-stamp{font-size: 15px;}
	}
	.spanDateTime {
		float: left;
		font: 400 13px/17px arial;
		color: #999;
	    margin-top: 7px;
	}
	.topDetail {
        border-top: 1px solid #eee;
	    border-bottom: 1px solid #eee;
	    float: left;
	    position: relative;
	    z-index: 6;
	    padding: 13px 0;
	    clear: both;
	    width: 100%;
	    margin-bottom: 10px;
	    clear: both;
		.block_share {
			a {
				border-bottom: 0;
			}
		}
		.box_comment {
			float: left;
			span {
				display: inline-block;
				font-size: 13px;
				margin-right: 5px;
				color: #0064b2;
			}
		}
		span {
			&:nth-child(1) {
				i {
					float: left;
					margin: 2px 2px 0 0;
				}
			}
			&:nth-child(2) {
				i {
					float: left;
					margin: 2px 2px 0 0;
				}
			}
		}
	}
	.title_sotap {
		float: left;
		width: 100%;
		border-bottom: 1px solid #ccc;
		padding-bottom: 5px;
		margin-bottom: 15px;
		label {
			font-size: 14px;
			font-family: Arial;
			color: #0064b2;
			border-bottom: 1px solid #0064b2;
			padding-bottom: 5px;
		}
	}
	.so_tap {
		float: left;
		width: 100%;
		margin-bottom: 15px;
		a {
			font-size: 14px;
			color: #666;
			border: 1px solid #ccc !important;
			padding: 9px 14px;
			border-radius: 3px;
			margin-right: 8px;
			cursor: pointer;
			float: left;
		}
		a.active {
			color: #fff;
			background: #0064b2;
			border: 1px solid #0064b2 !important;
		}
	}
}

.block_share {
	a {
		float: left;
		width: 32px; height: 32px;
		border-radius: 50%;
		margin: 0px 0 0 5px;
		position: relative;
		color: $colormain;
		&.btn_email{
			.icon-ns{width: 13px;height: 13px;}
		}
		.icon-ns {
		    position: absolute;
		    left: 0;
		    right: 0;
		    top: 0;
		    bottom: 0;
		    margin: auto;
		}
		&:first-of-type {
			margin-left: 0;
		}
		&:hover {
			background: #ededed;
		}
	}
	a.btn_facebook {
		
	}
	a.btn_twitter {
		
	}
	a.btn_google {
		
	}
	a.btn_email {
		
	}

	.count_comment {
	    font-size: 14px;
	    float: left;
	}
	
	/*New style*/
	&--left {
		float: left;
	    border: 1px solid #ddd;
	    border-radius: 5px;
	    .icon_commend {
		    width: auto;
		    height: 40px;
		    padding: 3px 10px;
		    border-radius: 0;
		    .icon-ns {
		    	float: left;
		    	position: relative;
	    	    margin:11px 3px 0 0;
		    }
		}
	    .count_comment {
	    	background: transparent;
	    	color: #333;
	    	font-size: 16px;
			margin-left: 2px;
		    margin-top: 6px;
	    	&:after {
	    		display: none;
	    	}
	    }
	}

	&--right {
	    float: right;
		margin-top: 5px;
		 .count_comment {
		 	padding: 7px; margin-right: 5px;
		 	&:after {
	    	    content: '';
			    position: absolute;
			    top: 12px;
			    margin-top: -4.5px;
			    right: -5px;
			    left: auto;
			    width: 0;
			    height: 0;
			    border-bottom: 6px solid transparent;
			    border-top: 6px solid transparent;
			    border-left: 6px solid #f6f6f6;
			    border-right: none;
		    }
		}
		.total{float: left;
			.bg_agray{
				background: #f6f6f6;position: relative;float: left;width: 100%;
				&:before{
				    content: '';
				    position: absolute;
				    top: 8px;
				    left: -7px;
				    width: 0;
				    height: 0;
				    border-bottom: 8px solid transparent;
				    border-top: 8px solid transparent;
				    border-right: 8px solid #f6f6f6;	
				}				
			}
		}		
	}

}



