/*CUSTOM GALLERY*/

.lg-toolbar {
	width: 60px; height: 100%;
	right: 0; left: auto; top: 0;
	background: #000;
}
.lg-sub-html_wrapper {
	width: 320px;
	height: 100%;
	right: 60px; left: auto; top: 0;
	background: #222222;
	padding: 95px 25px 95px 25px !important;
	text-align: left;
	h4, p {
		color: #d6d6d6;
		font: 400 px_to_rem(17px) 'Arial'; line-height: $lhbody;
	}
}
.lg-sub-html {
    position: relative;
	height: 100% !important;
	padding-right: 15px;
}
.lg-outer {
	.lg-inner {
	    max-width: calc(100% - 380px);
	    width: calc(100% - 380px);
	}
	.lg-thumb-outer {
	    width: calc(100% - 380px);
	    max-width: calc(100% - 380px);
        /*overflow: hidden;
    	left: 1px;*/
	}

	.lg-thumb-item {
    	border: none;
    	margin-bottom: 0;
	}
}

.lg-outer .lg-thumb-item img {
    height: auto;
    width: auto;
    display: block;
    position: absolute;
    top: 0;
    bottom: 0;
    margin: auto;
    max-height: 100%;
    left: 0;
    right: 0;
    max-width: 100%;
}

.lg-actions {
    position: absolute;
    width: 60px;
    right: 0;
    top: 230px;
    z-index: 99999;
    > div {
    	width: 36px;
	    height: 36px;
	    border-radius: 50% !important;
	    overflow: hidden;
	    border: 1px solid #dedede;
	    position: relative!important;
	    right: 0 !important;
	    left: 0 !important;
	    margin: 16px auto !important;
	    &:before, &:after {
		    left: 50%;
		    top: 50%;
		    position: absolute;
		    transform: translate(-50%, -50%);
	    }
    }
    .lg-next {

    }
    .lg-prev {

    }
}

.lg-outer .lg-thumb-outer.lg-grab .lg-thumb-item {
	position: relative;
    background: #222;
	&:not(.active) {
		&:before {
			content: "";
			width: 100%; height: 100%;
			position: absolute;
			top: 0; left: 0;
			background: rgba(12, 10, 10, 0.7);
			z-index: 1;
		}
	}
}

.lg-sub-html_wrapper:empty {
	display: none;
}

#lg-counter-current {
    color: #fff;
}

.lg-sub-html_wrapper:empty ~ .lg-inner {
	width: calc(100% - 60px) !important;
	max-width: calc(100% - 60px) !important;
}
.lg-sub-html_wrapper:empty ~ .lg-thumb-outer {
	width: calc(100% - 60px) !important;
	max-width: calc(100% - 60px) !important;
}

.lg-fullscreen-on {
	/*.lg-inner, .lg-thumb-outer {
		width: calc(100% - 60px) !important;
		max-width: calc(100% - 60px) !important;
	}
	.lg-sub-html_wrapper {
		display: none;
	}*/
	.lg-thumb-outer {
        -webkit-transform: translate3d(0, 100%, 0) !important;
    	transform: translate3d(0, 100%, 0) !important;
	}
	.lg-toggle-thumb {
		display: none !important;
	}
}

/* END CUSTOM GALLERY*/


/*DETAIL LONGFORM 1 */
#nytmm{
	#topbar{
		.container{padding: 0 15px;max-width: 100%;}
	}
}
#topbar {
  position: fixed;
  z-index: 98;
  top: 0;	
  height: 46px;
  border-bottom: 1px solid #eee;
  font-size: 12px;
  width: 100%;
  background-color: #fff;
  transition-duration: 200ms;
  transition-property: all;
  transition-timing-function: cubic-bezier(0.7, 1, 0.7, 1);
  transform: translateY(0);
  >.container {
    transition-duration: 400ms;
    transition-property: all;
    transition-timing-function: cubic-bezier(0.7, 1, 0.7, 1);
  } 
  #nytmm_sidenav {
    float: left;
    margin: 7px 0 0 30px;
    ul {
      li {
        display: inline-block;
        a {
          font-size: 14px;
          padding: 5px 20px;
          height: 30px;
          line-height: 30px;
          border-radius: 15px;
          margin-left: 5px;
          font-family: arial;  
          &:hover {
            background: #E2E2E2;
          }
        }
      }
    }
  }
  .block-right {
    margin-top: 6px;
    .search {
      margin: 0px 10px 0px 10px;
      .search-btn {
        top: 4px;
      }
    }
    .social {
      li {
        margin-left: 5px;
        a {
          color: #168fd6;
        }
      }
      .social-item {
        background: none;
        width: 15px;
        height: 15px;
        line-height: 15px;
        .ic {
          color: #999;
        }
      }
      .comment {
        position: relative;
        float: left;
        margin: 0 10px 0 0;
        .counter {
          margin-left: 5px;
        }
        &:before {
          content: "";
          background: #ccc;
          position: absolute;
          width: 1px;
          height: 15px;
          right: -9px;
        }
      }
    }
  }  
  .navbar-toggle {
    width: 28px;
    height: 28px;
    float: left;
    border: none;
    padding: 0;
    margin: 12px 20px 10px 0;
    background-color: transparent;
    cursor: pointer;
    outline: 0;
    .toggle-icon {
      position: relative;
      width: 18px;
      height: 2px;
      display: inline-block;
      background: #999;
      &:before {
        bottom: 12px;
        -webkit-transform: rotate(0);
        -moz-transform: rotate(0);
        transform: rotate(0);
        transition-duration: 400ms;
        transition-property: all;
        transition-timing-function: cubic-bezier(0.7, 1, 0.7, 1);
        position: absolute;
        left: 0;
        width: 18px;
        height: 2px;
        background: #999;
        content: " ";
      }
      &:after {
        position: absolute;
        left: 0;
        width: 18px;
        height: 2px;
        background: #999;
        content: " ";
        top: -6px;
        -webkit-transform: rotate(0);
        -moz-transform: rotate(0);
        transform: rotate(0);
        transition-duration: 400ms;
        transition-property: all;
        transition-timing-function: cubic-bezier(0.7, 1, 0.7, 1);
      }
    }
    &:hover {
      .toggle-icon {
        background: #9f224e;
        &:before {
          background: #9f224e;
        }
        &:after {
          background: #9f224e;
        }
      }
    }
    .toggle-icon.is-clicked {
      background: rgba(153,153,153,0);
      &:before {
        bottom: 5px;
        -webkit-transform: rotate(45deg);
        -moz-transform: rotate(45deg);
        transform: rotate(45deg);
        transition-duration: 400ms;
        transition-property: all;
        transition-timing-function: cubic-bezier(0.7, 1, 0.7, 1);
      }
      &:after {
        top: -5px;
        -webkit-transform: rotate(-45deg);
        -moz-transform: rotate(-45deg);
        transform: rotate(-45deg);
        transition-duration: 400ms;
        transition-property: all;
        transition-timing-function: cubic-bezier(0.7, 1, 0.7, 1);
      }
    }
  }
  .logo {
    display: inline-block;
